@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.app-logo {
  height: 60px;
  width: auto;
}

* {
  outline: 0px solid red;
}

.App {
  max-width: 1200px;
  margin: auto;
  padding: 0px 15px;
}

.App-header {
  background-color: #fff;
  margin: 24px 0px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.app-des {
  margin-top: 44px;
}

.icon-section {
  margin-top: 22px;
  margin-bottom: 12px;
}

.icon-section-header h1 {
  font-size: 18px;
  font-weight: bold;
}

.icon-card-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: flex-start;
}

.icon-card {
  margin: 22px 22px 0px 0px;
  border-radius: 4px;
  border: 1px solid #D8D8D8;
}

.icon-card-header {
  background: #D8D8D8;
  padding: 8px;
}

.icon-card-content {
  display: flex;
}

.icon-card-image {
  height: 55px;
  width: auto;
}

.icon-labeled {
  border-right: 1px solid #D8D8D8;
}

.icon-block {
  padding: 22px;
}

.h-600 {
  max-height: 600px
}

